.Dashboard{
    padding: 2em 4em;
    position: relative
}
.Dashboard > .header{
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    width: 100%;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.Dashboard .agregar{
    display: block;
    width: 100%;
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    margin-top: 2em
}
.Dashboard .agregar .add{
    background: #515eff;
    border: none;
    border-radius: 5px;
    color: white;
    padding: 1em 2em;
    font-weight: 600;
    font-family: 'Montserrat', system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue;
    cursor: pointer;
}


