.MenuLanding{
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    margin-bottom: 2em
}
.MenuLanding .menu{
    list-style: none;
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex
}
.MenuLanding .menu .item{
    margin-right: 1em
}
.MenuLanding .menu .item a{
    color: white;
    text-decoration: none;
}
.MenuLanding .menu .item .btn{
    background: #515eff;
    border: none;
    border-radius: 5px;
    color: white;
    padding: 1em 2em;
    font-weight: 600;
    font-family: 'Montserrat', system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue;
    cursor: pointer;
    font-weight: 400;
    margin: 0;
    padding: .5em 1em;
}


