.Login{
    text-align: center
}
.Login .title{
    color: #290062;
    font-weight: 600;
}
.Login .formulario{
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column
}
.Login .formulario .input{
    font-family: 'Montserrat', system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue;
    color: #301d6e;
    border: none;
    border-bottom: 1px solid #9087c0;
    margin-bottom: 1em;
    padding: .5em 1em;
    font-family: 16px;
    font-family: 1rem
}
.Login .formulario .input:focus{
    outline: none;
    border-bottom-color: #301d6e;
}
.Login .formulario .input::-webkit-input-placeholder{
    color: #9087c0;
}
.Login .formulario .input:-ms-input-placeholder{
    color: #9087c0;
}
.Login .formulario .input::-ms-input-placeholder{
    color: #9087c0;
}
.Login .formulario .input::placeholder{
    color: #9087c0;
}
.Login .formulario .btn{
    background: #515eff;
    border: none;
    border-radius: 5px;
    color: white;
    padding: 1em 2em;
    font-weight: 600;
    font-family: 'Montserrat', system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue;
    cursor: pointer;
}


