.Tareas{
    display: grid;
    grid-template-columns: 1fr 7fr;
    color: white;
}
.Tareas .Dashboard{
    background: #301d6e;
    min-height: 100vh;
}
@media (max-width: 799px){
    .Tareas{
        grid-template-columns: 1fr;
    }
}


