.Dash{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2em;
    margin-top: 3em;
}
.Dash .pin{
    background: #3b2883;
    border-radius: 0 0 5px 5px;
}
.Dash .pin:nth-of-type(2) .header{
    border-top-color: #ffde8e;
}
.Dash .pin:nth-of-type(3) .header{
    border-top-color: #1a834a;
}
.Dash .pin:nth-of-type(4) .header{
    border-top-color: #7fedf1;
}
.Dash .pin .header{
    background: #290062;
    padding: 1em 2em;
    border-top: 4px solid #ff5f89;
}
.Dash .pin .body{
    padding: 1em;
    min-height: 150px;
}
.Dash .pin .body .task{
    background: white;
    border-radius: 5px;
    color: #290062;
    margin-bottom: 1em;
    padding: 1em 2em;
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    cursor: pointer;
}
.Dash .pin .body .task .hora{
    color: #8168a2;
}
@media (max-width: 799px){
    .Dash{
        grid-template-columns: repeat(2,1fr);
    }
}
@media (max-width: 499px){
    .Dash{
        grid-template-columns: 1fr !important;
    }
}


