.Modal {
	display: block;
	opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 15%;
    right: 40%;
}
.Modal .Modal-dialog{
	overflow: hidden;
	border-radius: 5px;
	padding: 2em 4em 4em;
	-webkit-box-shadow: 1px 2px 3px #290062;
	        box-shadow: 1px 2px 3px #290062;
}
.Modal .Modal-dialog .cerrar{
	width: 100%;
	display: -ms-flexbox;
	display: -webkit-box;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
}
.Modal .Modal-dialog .cerrar button{
	background: none;
	color: #8168a2;
	border: none;
	font-size: 24px;
	font-size: 1.5rem;
	cursor: pointer;
}
.Modal .Modal-dialog .cerrar button:focus{
	outline: none;
}
.Modal .Modal-dialog .Modal-content .form{
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 2em;
}
.Modal .Modal-dialog .Modal-content .form .grupo{
	display: -ms-flexbox;
	display: -webkit-box;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
}
.Modal .Modal-dialog .Modal-content .form .grupo .label{
	margin-bottom: 1em;
	color: #290062;
}
.Modal .Modal-dialog .Modal-content .form .grupo .input{
	background: #9087c0;
	border: none;
	padding: 1em 2em;
	color: white;
	border-radius: 5px;
	font-family: 'Montserrat', system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue;
}
.Modal .Modal-dialog .Modal-content .form .grupo .input::-webkit-input-placeholder{
	color: rgba(255, 255, 255, 0.8);
}
.Modal .Modal-dialog .Modal-content .form .grupo .input:-ms-input-placeholder{
	color: rgba(255, 255, 255, 0.8);
}
.Modal .Modal-dialog .Modal-content .form .grupo .input::-ms-input-placeholder{
	color: rgba(255, 255, 255, 0.8);
}
.Modal .Modal-dialog .Modal-content .form .grupo .input::placeholder{
	color: rgba(255, 255, 255, 0.8);
}
.Modal .Modal-dialog .Modal-content .form .grupo .input:focus{
	outline: none;
}
.Modal .Modal-dialog .Modal-content .form .boton{
	-ms-flex-item-align: end;
	    align-self: end;
}
.Modal .Modal-dialog .Modal-content .form .boton .btn{
	background: #515eff;
	border: none;
	border-radius: 5px;
	color: white;
	padding: 1em 2em;
	font-weight: 600;
	font-family: 'Montserrat', system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue;
	cursor: pointer;
}
.Modal .Modal-dialog {
    opacity: 0;
    visibility: hidden;
    width: 80%;
    margin: 0 auto;
    background: white;
}
.Modal-content {
	color: #333;
}
@media (max-width: 799px){
	.Modal {
		right: 15%;
	}
}
@media (max-width: 499px){
	.Modal .Modal-dialog .Modal-content .form{
		grid-template-columns: 1fr;
	}
}



