.Search{
    display: grid;
    grid-template-columns: 1fr 5fr;
    overflow: hidden;
    border-radius: 5px;
    width: 40%;
}
.Search .icono{
    background: rgba(41, 0, 98, 0.5);
    padding: 1em 2em;
}
.Search .input{
    background: rgba(41, 0, 98, 0.5);
    border: none;
    padding: 1em 2em;
    color: white;
}
.Search .input::-webkit-input-placeholder{
    color: rgba(255, 255, 255, 0.8);
}
.Search .input:-ms-input-placeholder{
    color: rgba(255, 255, 255, 0.8);
}
.Search .input::-ms-input-placeholder{
    color: rgba(255, 255, 255, 0.8);
}
.Search .input::placeholder{
    color: rgba(255, 255, 255, 0.8);
}
.Search .input:focus{
    outline: none;
}
@media (max-width: 499px){
    .Search{
        display: none;
    }
}


