.Menu{
    background: #290062;
    min-height: 100vh;
    padding: 2em;
    text-align: center;
}
.Menu .nombre{
    border-bottom: 1px solid white;
    padding-bottom: 1em;
}
.Menu .menu{
    padding: 0;
    margin: 0;
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}
.Menu .menu .item{
    list-style: none;
    margin: 1em 0;
}
.Menu .menu .item a{
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    color: white;
    text-decoration: none;
    padding: 1em;
}
.Menu .menu .item a .icono{
    font-size: 48px;
    font-size: 3rem;
    color: #8168a2;
    margin-bottom: .2em;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}
.Menu .menu .item a:hover .icono{
    color: white;
}
@media (max-width: 799px){
    .Menu{
        min-height: auto;
        grid-row-start: 2;
    }
    .Menu .menu{
        -ms-flex-direction: row;
        flex-direction: row;
    }
}


