
*{
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}
body{
	font-family: 'Montserrat', system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue;
	margin: 0;
	padding: 0;
	overflow-x: hidden;

}
h1,h2,h3,h4,h5,figure,ul{
	margin: 0;
	padding: 0;
}
p{
	line-height: 1.5em;
}
img{
	width: 100%;
	vertical-align: top;
}



