.Perfil{
    position: relative
}
.Perfil .boton{
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer
}
.Perfil .boton span{
    margin-right: .5em;
}
.Perfil .boton .foto{
    width: 70px;
    border-radius: 5px;
    overflow: hidden
}
.Perfil .boton .foto img{
    width: 100%;
}
.Perfil .dropwdown-content{
    display: none;
    position: absolute;
    background: #f9f9f9;
    min-width: 160px;
    -webkit-box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, .2);
            box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, .2);
    border-radius: 0 0 5px 5px;
    z-index: 1;
    top: 100%;
    right: 0;
    overflow: hidden
}
.Perfil .dropwdown-content a{
    color: #290062;
    padding: 1em 2em;
    text-decoration: none;
    display: block
}
.Perfil .dropwdown-content a:hover{
    background: #3b2883;
    color: white;
}
.Perfil:hover .dropwdown-content{
    display: block;
}


